
.emoticon-container {
  width: 100%;
}
.emoticon-container .title-container {
  padding-top: 20px;
  padding-left: 5px;
  margin-bottom: 20px;
  font-size: 32px;
}
.emoticon-container .title-container .tag {
  color: #777;
  border: 2px solid #ccc;
  border-radius: 2px;
  font-size: 14px;
  display: inline-block;
  line-height: 30px;
  margin: 0 5px;
  padding: 0 15px;
}
.emoticon-container .title-container .tag:hover {
  background-color: #dfdfdf;
}
.emoticon-container .title-container .tag.active {
  background-color: #dfdfdf;
  box-shadow: 0px 0px 10px #dfdfdf;
}
.emoticon-container .category {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.emoticon-container .category .entry {
  margin: 10px;
  width: 300px;
}


